* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Libre Franklin', sans-serif;
  margin: 0;
  padding: 0;
  color: #fff;
  background-color: #000;
  // background: linear-gradient(180deg, #121212 0%, #000 50%);
  // transition: color 5s ease-out;
}

a {
  color: inherit;
  pointer-events: all;
}

#particleCanvas {
  visibility: hidden;
}

#container {
  // visibility: hidden;
  pointer-events: none;
  scroll-snap-type: y mandatory;

  section {
    position: relative;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    // transition: background-color 5s ease-out;

    // &:not(:last-of-type) { margin-bottom: 50vh; }

    .content {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

    .wrapper {
      width: 100%;
      padding: 0 18px;

      @media screen and (min-width:920px) {
        width: 800px;
        margin: 0 auto;
      }
    }
  }

  #home {
    h1 {
      font-size: 2.5rem;
      text-align: center;
      margin: 24px 0;
      letter-spacing: 1px;

      @media only screen and (min-width:720px) {
        font-size: 3.5rem;
      }
    }

    h2 {
      font-size: .9rem;
      font-weight: 200;
      margin-top: 0;

      @media only screen and (min-width:720px) {
        font-size: 1.15rem;
      }
    }

    nav {
      margin-top: 18px;
    }

    .links {
      a:not(:last-of-type) {
        margin-right: 12px;
      }

      i {
        font-size: 2.1rem;
      }
    }
  }

  #technologies {

    .grid {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
      grid-gap: 48px;


      &_icon_container {
        display: flex;
        flex-direction: column;
        align-items: center;

        i {
          font-size: 64px;
        }

        span {
          margin-top: 6px;
          text-align: center;
        }
      }
    }
  }

  #projects {
    opacity: .96;

    .content {
      color: #000;
      background-color: #fff;
      height: calc(100% - 5vw);
      width: calc(100% - 5vw);
    }
  }
}

footer {
  position: fixed;
  bottom: 0;
  right: 2.5vw;
  height: 2.5vw;
  display: flex;
  align-items: center;

  span {
    font-size: .94rem;
  }
}

canvas {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}